
.blog img{
  width: 100%;
  height: 100%;
}

.blog .text{
  padding: 40px 30px;
}

.blog  h1{
  font-weight: 500;
  font-size: 22px;
  line-height: 30px;
  margin: 20px 0;
  transition: 0..5s;
}
.blog h1:hover{
  color: rgb(74, 15, 25);
  cursor: pointer;
}

.blog span{
  text-transform: uppercase;
  font-weight: 500;
  color: rgb(74, 15, 25);
}

.blog span label{
  margin-left: 10px;
  color: gray;
  font-size: 14px;

}

.blog p{
  color: gray;
}