@import url('https://fonts.googleapis.com/css2?family=Playfair:ital,opsz,wght@0,5..1200,300..900;1,5..1200,300..900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,500&family=Rubik:ital,wght@0,300..900;1,300..900&display=swap');

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;

}

body{
  font-family: "Rubik", serif;
  
  
}


.container{
  max-width: 85%;
  margin: auto;
}

.flexSB{
  display: flex;
  justify-content: space-between;
}

.icon{
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 50%;
  text-align: center;
  transition: 0.5s;
  background-color:rgb(74, 15, 25);
  color: #fffdfd;
  
  
}

.icon:hover{
  cursor: pointer;
  background-color: rgb(131, 41, 41);
  
}



.row{
  width: 50%;
}

button{
  padding: 17px 30px;
  background-color: #fff;
  color: rgb(74, 15, 25);
  font-weight: 600;
  border: none;
  margin: 30px 10px 0 0 ;
  background-repeat: 3px;
  box-shadow: 0 24px 36px -11px rgba(0 0 0/9%);
  cursor: pointer;
}

button i{
  margin-left: 20px;
  
}

button:hover{
  box-shadow: none;
  cursor: pointer;
  
  
}

.primary-btn{
  background-color: rgb(74, 15, 25);
  color: #fff;
  cursor: pointer;


}



#heading{
  text-align: center;
  padding: 40px 0 ;
}

#heading h3{
  font-weight: 600;
  letter-spacing: 1px;
}

#heading h1{
  font-size: 45px;
  margin: 20px 0;
  text-transform: capitalize;
}

p{
  line-height: 30px;
  font-size: 18px;
}

.back{
  background-image: url(../public/images/about.jpg);
  background-size: cover;
  background-attachment:fixed ;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100vh;
  padding-right: 50px;
  padding-top: 20%;
  text-align: center;

} 

.back h1{
  font-size: 100px;
  font-weight: 400;

}

.back h2{
  font-size: 17px;
  font-weight: 500;
  text-transform: uppercase;
  
}

.margin{
  margin-top: 40.3%;
}

.grid{
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-gap: 30px;
}

.flex{
  display: flex;
}
.grid2{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  grid-gap: 30px;
}

.outline-btn{
  margin: 0;
  border: 2px solid #f3f3f3;
  width:100%;
  transition: 0.5s;
  box-shadow: none;
  font-size: 17px; 
  font-weight: 500;
  color: black;
}

.outline-btn:hover{
  background-color: rgb(71, 70, 70);
  color: #fff;
  cursor: pointer;
}

.padding{
  padding: 80px 0;
}



.shadow{
  box-shadow: 0 5px 25px -2px rgba(0 0 0/6%);
  
}

.grid3{
  display: grid;
  grid-template-columns: repeat(6,1fr);
  grid-gap: 10px;
  
}

@media(max-width:768px){


  .grid,.grid3,.grid2{
    grid-template-columns: repeat(2,1fr);
    
  }
.grid2{
    grid-template-columns: repeat(1,1fr);
    
  }

  .back{
    background-position: center;
    padding-top: 50%;
    height: 80vh;
  }
  .margin{
    margin-top: 85%;
  }
}